import { Pressable, StyleSheet, Text, View, TextInput } from 'react-native'
import React, { useState } from 'react'

export default function Order({ price, sendMail }) {

    const [current, setCurrent] = useState();
    const [amountState, setamountState] = useState(1);
    const [shadow, setShadow] = useState(false);

    let amounts = [
        1, 2, 5, 10, 25, 50, 100
    ]
    return (
        <View style={{ alignItems: "center" }}>
            <Text style={{ fontSize: 15, fontWeight: "700", }}>בחר כמות</Text>
            <View style={{ display: "flex", flexDirection: "column" }}>
                {amounts.map((e, i) => {
                    return (
                        <Pressable onPress={() => setCurrent(i)} key={i} style={{ flexDirection: "row-reverse", width: "10vw", justifyContent: "space-between", backgroundColor: current === i ? "#ffe6d0" : "", }}>
                            <Text style={{ fontSize: 15, fontWeight: "600", lineHeight: 25 }}>{e}</Text>
                            <Text style={{ fontSize: 15, fontWeight: "600", lineHeight: 25 }}>{price * e}₪</Text>
                        </Pressable>
                    )
                })}
            </View>
            <View style={{flexDirection: "row", alignItems: "center", marginTop: 10, width: "100%", justifyContent: "center"}}>
                <View
                    nativeID='count'
                    style={[{
                        flexDirection: "row",
                        alignItems: "center",
                        justifyContent: "space-between",
                        borderColor: "#1abc9c",
                        borderWidth: 1,
                        borderRadius: "0.2vw"
                    }, shadow && {
                        shadowColor: "black",
                        shadowOffset: { width: 0, height: 0 },
                        shadowOpacity: 0.4,
                        shadowRadius: 5
                    }]}>
                    <TextInput maxLength={2} value={amountState}
                        onFocus={() => setShadow(!shadow)}
                        onBlur={() => setShadow(!shadow)}
                        editable={false}
                        style={{
                            outlineStyle: "none",
                            width: 50,
                            color: "#1abc9c",
                            padding: "0.2vw",
                            textAlignVertical: "center",
                            textAlign: "center",
                            fontSize: "1.5vw",
                        }}
                        type="num" onChangeText={(num) => setWidth(num)} />
                    <View>
                        <Pressable onPress={() => {
                            setamountState(amountState + 1)
                        }} style={[styles.btn, { borderTopRightRadius: "0.1vw", }]}>
                            <Text style={styles.text}>+</Text>
                        </Pressable>
                        <Pressable onPress={() => {
                            setamountState(amountState > 0 ? amountState - 1 : 0);
                        }} style={[styles.btn, { borderBottomRightRadius: "0.1vw", }]}>
                            <Text style={styles.text}>-</Text>
                        </Pressable>
                    </View>
                </View>
                <Pressable style={{backgroundColor: "#5559e0", padding: 9, marginLeft: 25}} onPress={() => sendMail(amountState)}>
                    <Text style={{color: "white", fontWeight: "600", fontSize: 19}}>הוספה לסל {price * amountState}₪</Text>
                </Pressable>
            </View>
        </View>
    )
}

const styles = StyleSheet.create({
    btn: {
        borderColor: "#d8d8d8",
        borderWidth: 1,
        width: "2vw",
        textAlign: "center"
    },
    text: {
        fontSize: "1.1vw"
    }
})