import { Modal, Pressable, ScrollView, StyleSheet, Text, View } from 'react-native'
import React, { useEffect, useRef, useState } from 'react';
import Popup from './Popup';

export default function Colors({ color, colorsArr, setColor, setDark }) {
    const popupRef = useRef(null);
    const [flag, setFlag] = useState(false);
    const [shadow, setShadow] = useState(false);
    useEffect(() => {
        window.addEventListener("mousedown", ({ target }) => {
            if (popupRef.current && !flag && !popupRef.current.contains(target)) {
                setFlag(false);
            }
        })
    }, [flag, popupRef]);
    return (
        <View style={{ direction: "rtl" }}>
            <Text style={{ fontSize: "1.6vw", color: "gray", fontWeight: "500" }}>צבע</Text>
            <Pressable
                onFocus={() => setShadow(true)}
                onBlur={() => setShadow(false)}
                onPress={() => setFlag(!flag)}
                style={{
                    direction: "rtl",
                    borderColor: shadow ? "#1abc9c" : "#c9c9c9",
                    borderWidth: 1,
                    fontSize: "1.8vw",
                    width: "40vw",
                    height: "8vh",
                    padding: "0.2vw",
                    outlineStyle: "none",
                    flexDirection: "row",
                    alignItems: "center"
                }}
            >
                <View style={{ borderWidth: 1, borderRadius: "50%", borderColor: "black", width: "2vw", height: "2vw", backgroundColor: color.color }} />
                <Text
                    style={{ marginRight: "0.5vw", fontSize: "1.4vw", lineHeight: "1.4vw", color: "#1abc9c", fontWeight: "500" }}
                >{color.name}</Text>
            </Pressable>
            <Popup flag={flag} setFlag={setFlag} popupRef={popupRef}>
                {Array.isArray(colorsArr) && colorsArr.map((e, i) => {
                    return (
                        <Pressable
                            key={i}
                            onPress={() => { setColor(e); setDark(e.dark); setFlag(!flag) }}
                            style={({ hovered }) => [{
                                display: "flex",
                                flexDirection: "column",
                                width: "10vw",
                                alignItems: "center",
                                paddingVertical: "0.3vw"
                            },]}
                        >
                            {({ hovered }) => (
                                <>
                                    <View
                                        nativeID={hovered && "popup"}
                                        style={[{
                                            width: "5.6vw",
                                            height: "5.6vw",
                                            borderRadius: "50%",
                                            borderColor: "black",
                                            borderWidth: 1,
                                            backgroundColor: e.color,
                                        },]}
                                    />
                                    <Text style={{fontWeight: hovered ? "bold" : ""}}>{e.name}</Text>
                                </>
                            )}
                        </Pressable>
                    )
                })}
            </Popup>
        </View>
    )
}

const styles = StyleSheet.create({})