import { StyleSheet, Text, TextInput, View } from 'react-native';
import React, { useState } from 'react';

export default function TextUser({text, setText, length, inputRef}) {
  const [shadow, setShadow] = useState(false);
  return (
    <View style={{direction: "rtl"}}>
      <Text style={{fontSize: "1.6vw", color: "gray", fontWeight: "500"}}>טקסט: <Text style={{fontSize: "1vw", color: "gray"}}>נא לכתוב בזהירות</Text></Text>
      <TextInput
      ref={inputRef}
      value={text} 
      style={[{
        borderColor: shadow ? "#1abc9c" : "#c9c9c9", 
        borderWidth: 1, 
        fontSize: "1.8vw", 
        width: "40vw",
        height: "8vh",
        paddingHorizontal: 10, 
        outlineStyle: "none"
        }, shadow && {
          shadowColor: "black",
          shadowOffset: {width: 0, height: 0},
          shadowOpacity: 0.4,
          shadowRadius: 5
      }]} 
      onFocus={() => setShadow(!shadow)}
      onBlur={() => setShadow(!shadow)}
      onChangeText={(txt) => setText(txt)}
       maxLength={length} />
      <Text style={{textAlign: "right", color: "gray", fontWeight: "500"}}>{24 - text?.length} תווים נשארו </Text>
    </View>
  )
}

const styles = StyleSheet.create({})