import { Modal, Pressable, ScrollView, StyleSheet, Text, View } from 'react-native'
import React from 'react'

export default function Popup({ flag, setFlag, children, popupRef, type }) {
    return (
        <Modal visible={flag} transparent={true} animationType="fade">
            <View style={{
                // backgroundColor: "#0000008c",
                alignItems: "center",
                justifyContent: "center",
                height: "100%"
            }}>
                <View
                    nativeID='popup'
                    style={{
                        width: "40vw",
                        backgroundColor: "#fff",
                        alignItems: "center",
                        borderRadius: "2vw"
                        // paddingHorizontal: "3vw",
                        // paddingVertical: "3vw",
                    }}
                    ref={popupRef}
                >
                    <View
                        style={{ position: "absolute", top: "1.5vw", right: "1.5vw", paddingHorizontal: "vw", }}
                    >
                        <Pressable onPress={() => setFlag(!flag)}>
                            {({ hovered }) =>
                                (<Text style={{ color: "gray", fontSize: "1.1vw", fontWeight: "500", lineHeight: "1.1vw", transform: [{scale: hovered ? 1.1 : 1}] }}>X</Text>)}
                        </Pressable>
                    </View>
                    <Text style={{ color: "gray", marginVertical: "0.3vw", fontSize: "1.6vw", fontWeight: "500" }}>בחר פונט</Text>
                    <ScrollView
                        style={{
                            maxHeight: "30vw",
                            marginVertical: "3vw",
                            width: "100%"
                        }}
                        contentContainerStyle={[type === "font" ? {
                            alignItems: "center",
                            justifyContent: "flex-start"
                        } : {
                            flexWrap: "wrap",
                            flexDirection: "row",
                            alignItems: "center",
                            justifyContent: "center"
                        }]}
                    >
                        {children}
                    </ScrollView>
                </View>
            </View>
        </Modal>
    )
}

const styles = StyleSheet.create({})