import { ImageBackground, PanResponder, StyleSheet, View, Animated } from 'react-native'
import React, { useEffect, useMemo, useRef, useState } from 'react';
import Svg, { SvgProps, Path, Rect, G, Text } from "react-native-svg";
import Lottie from "lottie-react";
import Loading from "../loading.json";
import TextToSVG from "text-to-svg";
// import Text from 'react-svg-text';


export default function Sticy({ text, color, font, dark, inputRef, width, setText }) {
    const pan = useRef(new Animated.ValueXY()).current;
    const textRef = useRef(null);
    const imgRef = useRef(null);

    // 76 מינימום לסטנדרט
    // 94 מקסימום לסטנדרט
    // 1096 * 132

    // 57 מינימום למיני
    // 76 מקסימום למיני
    // 945 * 94


    const responder = useRef(
        PanResponder.create({
            onMoveShouldSetPanResponder: () => true,
            onPanResponderGrant: ({ touchHistory }) => {
                pan.setOffset({
                    y: pan.y._value,
                    x: pan.x._value
                });
            },
            onPanResponderMove: Animated.event(
                [
                    null,
                    { dy: pan.y, dx: pan.x },
                ],
                {
                    useNativeDriver: true
                }),
            onPanResponderRelease: (event, state) => {
                pan.flattenOffset();
            }
        })
    ).current;

    const [background, setBackground] = useState(dark ? "#0000007d" : "#f0f0f0");
    const [fontSize, setFontSize] = useState(width === "s" ? 94 : 76);
    const [minFontSize, setMinFontSize] = useState(width === "s" ? 76 : 57);
    const [maxFontSize, setMaxFontSize] = useState(width === "s" ? 94 : 76);
    const [widthCurrent, setWidthCurrent] = useState(width === "s" ? 1096 : 945);
    const [heightCurrent, setHeightCurrent] = useState(width === "s" ? 132 : 94);
    const [style, setStyle] = useState();
    const [loading, setLoading] = useState(false);
    const [path, setPath] = useState(null);

    const checkFontsize = async (text) => {
        let textTag = document.querySelector("#text");


        if (textTag) {

            let widthText = textTag?.getBoundingClientRect().width + (text?.match(/w/g)?.length > 1 ? 60 : 30);
            let divWidth = textRef.current?.getBoundingClientRect().width;
            if (widthText > divWidth) {

                inputRef.current.maxLength = 24;

                for (let i = fontSize; i >= minFontSize; i -= 1) {
                    textTag.style.fontSize = i;
                    widthText = textTag.getBBox().width + (text?.match(/w/g)?.length > 1 ? 60 : 30);
                    divWidth = textRef.current?.getBoundingClientRect().width;
                    if (widthText < divWidth) {
                        await setFontSize(i);
                        break;
                    } else {
                        continue;
                    }
                }
                if (widthText > divWidth) {
                    setText(text.slice(0, text.length - 1));
                    inputRef.current.maxLength = text?.length - 1;
                }
            } else {
                if (fontSize < maxFontSize) {
                    for (let j = fontSize; j <= maxFontSize; j += 1) {
                        textTag.style.fontSize = j;
                        widthText = textTag?.getBoundingClientRect().width + (text?.match(/w/g)?.length > 1 ? 60 : 30);
                        divWidth = textRef.current?.getBoundingClientRect().width;
                        if (widthText + (text?.match(/w/g)?.length > 1 ? 60 : 30) < divWidth) {
                            continue;
                        } else {
                            await setFontSize(j);
                            break;
                        }
                    }
                    if (widthText < divWidth && inputRef.current.length < 24) {
                        setText(text.slice(0, text.length + 1));
                        inputRef.current.maxLength = text?.length + 1;
                    }
                }
            }
        }
    }

    useMemo(() => {
        setBackground(dark ? "#0000007d" : "#f0f0f0");
    }, [dark]);

    const changeSize = async () => {
        await setWidthCurrent(width === "s" ? 1096 : 945);
        await setHeightCurrent(width === "s" ? 132 : 94);
        await setFontSize(width === "s" ? 94 : 76);
        await setMinFontSize(width === "s" ? 76 : 57);
        await setMaxFontSize(width === "s" ? 94 : 76);
        await setLoading(true);
        setPath(false);
        setTimeout(() => {
            setLoading(false);
        }, 1000);
        setTimeout(() => {
            checkFontsize(text);
        }, 500);
    }

    useMemo(() => {
        changeSize();
    }, [width]);


    useMemo(() => {
        setLoading(true);
        setPath(false);
        setTimeout(() => {
            setLoading(false);
        }, 1000);
        setTimeout(() => {
            checkFontsize(text);
        }, 500);
    }, [font, text]);

    const getDPath = (style) => {
        return new Promise((reslove, reject) => {
            if (font.path) {
                TextToSVG.load(font.path, (err, textToSVG) => {
                    // debugger
                    const svg1 = textToSVG.getD(text,
                        {
                            ...style,
                            kerning: false,
                            tracking: true
                        }
                    );
                    setPath(svg1);
                    reslove(svg1);
                });
            }
        })
    }
    useMemo(() => {
        setLoading(true);
        setTimeout(() => {
            setLoading(false);
        }, 600);
    }, [color]);
    useMemo(() => {
        getDPath(style);
    }, [style]);

    const createPath = async () => {
        setPath(false);
        await checkFontsize(text);
        setTimeout(() => {
            setStyle({
                width: "max-content",
                fontStyle: font.italic ? "italic" : "normal",
                fontSize: fontSize,
                fontFamily: font.name,
                color: color,
                fontWeight: font.bold ? "bold" : "",
                lineHeight: fontSize,
                letterSpacing: font.latterSpacing ? font.latterSpacing : "0",
                x: width === "s" ? 15 : 11,
                y: heightCurrent / 1.3
            });
        }, 500);
    }
    useEffect(() => {
        createPath();
    }, [fontSize, color, font, text]);

    return (
        <View style={{ flex: 1, alignItems: "center", maxHeight: "19vw" }}>
            <View
                ref={imgRef}
                nativeID='image-background'
                style={{
                    flexDirection: 'row',
                    alignItems: "center",
                    width: widthCurrent,
                    height: heightCurrent + 20,
                    backgroundColor: background,
                }}
            >
                <View
                    nativeID='image-background2'
                    style={{
                        flexDirection: 'row',
                        alignItems: "center",
                        justifyContent: "center",
                        paddingLeft: "15px",
                        width: widthCurrent,
                        height: heightCurrent + 20,
                    }}
                >
                    {<>
                        {loading && <View
                            nativeID='loading'
                            style={{
                                width: "100%",
                                height: heightCurrent + 20,
                                position: "absolute",
                                zIndex: 100,
                                justifyContent: "center",
                                alignItems: "center",
                                left: 0
                            }}>
                            <Lottie
                                animationData={Loading}
                                style={{ width: "100px", height: "100px", }}
                                autoPlay={true}
                                loop={true}
                            />
                        </View>}
                        {/* <Svg
                            xmlns="http://www.w3.org/2000/svg"
                            width={heightCurrent}
                            height={heightCurrent}
                            fill="currentColor"
                            className="bi bi-instagram"
                            stroke={""}
                            viewBox="-0.6 -0.6 17 17"
                            style={{ color: color }}
                        >
                            <Path d="M8 0C5.829 0 5.556.01 4.703.048 3.85.088 3.269.222 2.76.42a3.917 3.917 0 0 0-1.417.923A3.927 3.927 0 0 0 .42 2.76C.222 3.268.087 3.85.048 4.7.01 5.555 0 5.827 0 8.001c0 2.172.01 2.444.048 3.297.04.852.174 1.433.372 1.942.205.526.478.972.923 1.417.444.445.89.719 1.416.923.51.198 1.09.333 1.942.372C5.555 15.99 5.827 16 8 16s2.444-.01 3.298-.048c.851-.04 1.434-.174 1.943-.372a3.916 3.916 0 0 0 1.416-.923c.445-.445.718-.891.923-1.417.197-.509.332-1.09.372-1.942C15.99 10.445 16 10.173 16 8s-.01-2.445-.048-3.299c-.04-.851-.175-1.433-.372-1.941a3.926 3.926 0 0 0-.923-1.417A3.911 3.911 0 0 0 13.24.42c-.51-.198-1.092-.333-1.943-.372C10.443.01 10.172 0 7.998 0h.003zm-.717 1.442h.718c2.136 0 2.389.007 3.232.046.78.035 1.204.166 1.486.275.373.145.64.319.92.599.28.28.453.546.598.92.11.281.24.705.275 1.485.039.843.047 1.096.047 3.231s-.008 2.389-.047 3.232c-.035.78-.166 1.203-.275 1.485a2.47 2.47 0 0 1-.599.919c-.28.28-.546.453-.92.598-.28.11-.704.24-1.485.276-.843.038-1.096.047-3.232.047s-2.39-.009-3.233-.047c-.78-.036-1.203-.166-1.485-.276a2.478 2.478 0 0 1-.92-.598 2.48 2.48 0 0 1-.6-.92c-.109-.281-.24-.705-.275-1.485-.038-.843-.046-1.096-.046-3.233 0-2.136.008-2.388.046-3.231.036-.78.166-1.204.276-1.486.145-.373.319-.64.599-.92.28-.28.546-.453.92-.598.282-.11.705-.24 1.485-.276.738-.034 1.024-.044 2.515-.045v.002zm4.988 1.328a.96.96 0 1 0 0 1.92.96.96 0 0 0 0-1.92zm-4.27 1.122a4.109 4.109 0 1 0 0 8.217 4.109 4.109 0 0 0 0-8.217zm0 1.441a2.667 2.667 0 1 1 0 5.334 2.667 2.667 0 0 1 0-5.334z" />
                        </Svg> */}
                        <Svg
                            version="1.0"
                            xmlns="http://www.w3.org/2000/svg"
                            width={heightCurrent}
                            height={heightCurrent}
                            viewBox="0 0 512.000000 512.000000"
                            preserveAspectRatio="xMidYMid meet"
                        >
                            <G
                                transform="translate(0.000000,512.000000) scale(0.100000,-0.100000)"
                                fill="currentColor"
                                style={{ color: color }}
                                stroke="none"
                            >
                                <Path
                                    d="M1220 5105 c-628 -87 -1121 -584 -1206 -1215 -20 -155 -20 -2505 0
-2660 42 -312 180 -592 402 -814 221 -221 502 -360 814 -402 155 -20 2505 -20
2660 0 636 86 1130 580 1216 1216 20 155 20 2505 0 2660 -86 636 -580 1130
-1216 1216 -148 19 -2526 19 -2670 -1z m2562 -445 c244 -23 445 -118 609 -287
160 -165 246 -354 269 -591 13 -144 13 -2300 0 -2444 -23 -237 -109 -426 -269
-591 -164 -169 -365 -264 -609 -287 -144 -13 -2300 -13 -2444 0 -244 23 -445
118 -609 287 -160 165 -246 354 -269 591 -13 144 -13 2300 0 2444 23 244 118
445 287 609 159 155 356 247 573 268 127 12 2333 13 2462 1z"/>
                                <Path
                                    d="M3813 4239 c-75 -29 -144 -95 -178 -171 -97 -212 50 -453 284 -466
113 -6 228 51 290 145 41 61 55 113 54 196 -2 222 -238 378 -450 296z"/>
                                <Path
                                    d="M2380 3865 c-290 -41 -542 -167 -750 -375 -224 -223 -348 -488 -381
-813 -33 -311 66 -658 260 -912 58 -77 179 -198 256 -256 254 -194 601 -293
913 -260 324 33 589 157 812 381 224 223 348 488 381 812 27 257 -35 543 -168
773 -268 466 -793 724 -1323 650z m380 -465 c311 -75 565 -329 640 -640 27
-116 27 -284 0 -400 -59 -248 -233 -463 -463 -575 -126 -61 -233 -85 -377 -85
-144 0 -251 24 -377 85 -230 112 -404 327 -463 575 -27 116 -27 284 0 400 74
308 327 564 635 639 112 28 291 28 405 1z"/>
                            </G>
                        </Svg>
                        <Animated.View
                            style={{ transform: [{ translateY: pan.y }, { translateX: pan.x }], flex: 1, cursor: "all-scroll" }}
                            {...responder.panHandlers}
                            ref={textRef}
                        >
                            <Svg
                                fill={color}
                                stroke={""}
                                style={{
                                    alignItems: "center",
                                    justifyContent: "center",
                                    height: heightCurrent,
                                }}
                                id="text-svg"
                            >
                                {path ? <Path
                                    x={0}
                                    y={10}
                                    d={path}
                                // style={{
                                //     width: "max-content",
                                //     fontStyle: font.italic ? "italic" : "normal",
                                //     fontSize: `${fontSize}px`,
                                //     fontFamily: font.name,
                                //     color: color,
                                //     fontWeight: font.bold ? "bold" : "",
                                //     lineHeight: `${fontSize}px`,
                                //     letterSpacing: font.latterSpacing ? font.latterSpacing : "0"
                                // }}
                                /> : <Text
                                    x={0}
                                    y={width === "s" ? heightCurrent / 3.5 : heightCurrent / 4.5}
                                    id="text"
                                    style={{
                                        width: "max-content",
                                        fontStyle: font.italic ? "italic" : "normal",
                                        fontSize: `${fontSize}px`,
                                        fontFamily: font.name,
                                        color: color,
                                        fontWeight: font.bold ? "bold" : "",
                                        lineHeight: `${fontSize}px`,
                                        letterSpacing: font.latterSpacing ? font.latterSpacing : "0"
                                    }}
                                    verticalAnchor="start"
                                >{font.upper ? text.toUpperCase() : text}</Text>}
                            </Svg>
                        </Animated.View>
                    </>
                    }
                </View>
            </View>
        </View>
    )
}

const styles = StyleSheet.create({})


