import { Pressable, StyleSheet, Text, View, Modal, ScrollView } from 'react-native'
import React, { useEffect, useRef, useState } from 'react'
import Popup from './Popup';

export default function Fonts({ text, font, fontsArr, setFont, sort }) {
    const popupRef = useRef(null);
    const [flag, setFlag] = useState(false);
    const [shadow, setShadow] = useState(false);
    useEffect(() => {
        window.addEventListener("mousedown", ({ target }) => {
            if (popupRef.current && !flag && !popupRef.current.contains(target)) {
                setFlag(false);
            }
        })
    }, [flag, popupRef]);
    return (
        <View style={{ direction: "rtl" }}>
            <Text style={{ fontSize: "1.6vw", color: "gray", fontWeight: "500" }}>פונט</Text>
            <Pressable
                onFocus={() => setShadow(true)}
                onBlur={() => setShadow(false)}
                onPress={() => setFlag(!flag)}
                style={{
                    direction: "rtl",
                    borderColor: shadow ? "#1abc9c" : "#c9c9c9",
                    borderWidth: 1,
                    fontSize: "1.8vw",
                    width: "40vw",
                    height: "8vh",
                    padding: "0.2vw",
                    outlineStyle: "none",
                    flexDirection: "row",
                    alignItems: "center"
                }}
            >
                <Text
                    style={{ marginRight: "0.6vw", fontSize: "1.5vw", lineHeight: "0.7vw", color: "#1abc9c", fontWeight: "500", fontFamily: font.name }}
                >{text}</Text>
            </Pressable>
            <Popup flag={flag} setFlag={setFlag} popupRef={popupRef} type={"font"}>
                {Array.isArray(fontsArr) && fontsArr.map((e, i) => {
                    return (
                        <Pressable
                            key={i}
                            onPress={() => { setFont(e); setFlag(!flag); }}
                            style={({ hovered }) => [{
                                display: "flex",
                                flexDirection: "column",
                                width: "30vw",
                                alignItems: "center",
                                paddingVertical: "0.8vw",
                                marginVertical: "0.4vw",
                            }, hovered && { backgroundColor: "#c3c3c3" }, e.name === font.name && {
                                borderColor: "#a5a4a4",
                                borderWidth: 1,
                                shadowColor: "black",
                                shadowOffset: { width: 0, height: 0 },
                                shadowOpacity: 0.4,
                                shadowRadius: 5
                            }]}
                        >
                            <Text style={{ color: "gray", fontWeight: "400", fontSize: "1.2vw" }}>Font: {e.name}</Text>
                            <Text style={{ color: "gray", fontFamily: e.name, fontWeight: "500", fontSize: "1.5vw" }}>{e.upper ? text.toUpperCase() : text }</Text>
                        </Pressable>
                    )
                })}
            </Popup>
        </View>
    )
}

const styles = StyleSheet.create({})